export function makeAll(state: any) {
  return (
    state.description +
    "\n" +
    state.input +
    "\n" +
    state.output +
    "\n" +
    state.example +
    "\n" +
    state.limits
  );
}

export function findSections(markdown: string[]) {
  const sections = [
    "# Descripcin",
    "# Entrada",
    "# Salida",
    "# Ejemplo",
    "# Lmites",
  ];
  let sectionInfo: string[][] = [];
  let info: string[] = [];
  let sectionIndex = 0;
  markdown.forEach((line) => {
    if (line.trim() === sections[sectionIndex]) {
      sectionInfo.push(info);
      sectionIndex++;
      info = [];
    }
    info.push(line);
  });
  sectionInfo.push(info);
  return sectionInfo;
}

